<template>
  <div>
    <van-search
      v-model="value"
      placeholder="请输入店铺区域进行搜索"
      show-action
      shape="round"
      @search="onSearch"
    >
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>
    <div id="map"></div>
  </div>
</template>

<script>
import BMap from "BMap";
import { Search, Toast } from "vant";

export default {
  name: "Map",
  components: {
    [Search.name]: Search,
    [Toast.name]: Toast
  },
  data() {
    return {
      value: "",
      mapDom: "",
      markerArr: [
        {
          title: "广州火车站店(远元修脚房)",
          point: "113.324531,23.157003",
          address: "广东省广州市广州火车站",
          tel: "12306445252",
          id: 6
        },
        {
          title: "广州塔(远元修脚房）",
          point: "113.330934,23.113401",
          address: "广东省广州市广州塔（赤岗塔） ",
          tel: "18500000000",
          id: 6
        },
        {
          title: "广州动物园(远元修脚房)",
          point: "113.312213,23.147267",
          address: "广东省广州市广州动物园",
          tel: "18500000000",
          id: 6
        },
        {
          title: "天河公园(远元修脚房)",
          point: "113.372867,23.134274",
          address: "广东省广州市天河公园",
          tel: "18500000000",
          id: 6
        }
      ]
    };
  },
  methods: {
    onSearch() {
      //地图搜索
      var map = this.mapDom;
      var _this = this;
      var localSearch = new BMap.LocalSearch(map);
      map.clearOverlays(); //清空原来的标注
      var keyword = this.value;
      if (keyword.trim().length === 0) return Toast("请输入位置信息");
      localSearch.setSearchCompleteCallback(function(searchResult) {
        var poi = searchResult.getPoi(0);
        // map.centerAndZoom(poi.point, 15);
        console.log(poi.point, "poi.point");
        // 创建地理编码实例
        var myGeo = new BMap.Geocoder();
        // 根据坐标得到地址描述
        myGeo.getLocation(
          new BMap.Point(poi.point.lng, poi.point.lat),
          function(result) {
            if (result) {
              var addressComponents = result.addressComponents;
              var province = addressComponents.province;
              var city = addressComponents.city;
              var district = addressComponents.district;
              var userpoi = result.point;
              //   console.log(result.point, "地址搜索获取经纬度");

              //用户地址描述
              var mk = new BMap.Marker(userpoi);
              map.addOverlay(mk);
            //   map.panTo(userpoi);
              var userlabel = new BMap.Label('当前搜索位置', {
                  offset: new BMap.Size(-20,25)
                });
              mk.setLabel(userlabel);
              console.log("搜索获取经纬度", userpoi);
              map.centerAndZoom(userpoi, 15);

              var markerArr = _this.markerArr; //地图标注点
              var point = new Array(); //存放标注点经纬信息的数组
              var marker = new Array(); //存放标注点对象的数组
              var info = new Array(); //存放提示信息窗口对象的数组
              var centerAdd = new BMap.Point(113.324531, 23.157003);
              // map.centerAndZoom(centerAdd, 15);

              for (var i = 0; i < markerArr.length; i++) {
                var p0 = markerArr[i].point.split(",")[0]; //
                var p1 = markerArr[i].point.split(",")[1]; //按照原数组的point格式将地图点坐标的经纬度分别提出来
                point[i] = new BMap.Point(p0, p1); //循环生成新的地图点
                marker[i] = new BMap.Marker(point[i]); //按照地图点坐标生成标记
                map.addOverlay(marker[i]);
                marker[i].setAnimation(BMAP_ANIMATION_BOUNCE); //跳动的动画
                var label = new BMap.Label(markerArr[i].title, {
                  offset: new BMap.Size(20, -10)
                });
                marker[i].setLabel(label);
                info[i] = new BMap.InfoWindow(
                  "<p style='font-size:.24rem;height:.45rem;'><strong style='font-size:.3rem'>" +
                    markerArr[i].title +
                    "</strong></br>地址：" +
                    markerArr[i].address +
                    "</br> 电话：" +
                    markerArr[i].tel +
                    "</br></p>"
                ); // 创建信息窗口对象
              }
              for (let i = 0; i < marker.length; i++) {
                const element = marker[i];
                // marker[i].setAnimation(BMAP_ANIMATION_BOUNCE); //跳动的动画
                marker[i].addEventListener("click", function() {
                  this.openInfoWindow(info[i]);
                  return false;
                });
              }
            }
          }
        );
      });
      localSearch.search(keyword);
    },
    createMap() {
      // 创建Map实例
      var map = new BMap.Map("map");
      this.mapDom = map; //保存地图对象
      // 初始化地图,设置中心点坐标和地图级别
      var point = new BMap.Point(116.403847, 39.915526); //初始位置
      map.centerAndZoom(point, 18);
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(
        //默认获取用户诶只
        function(r) {
          if (this.getStatus() == BMAP_STATUS_SUCCESS) {
            var mk = new BMap.Marker(r.point);
            map.addOverlay(mk);
            map.panTo(r.point);
            console.log("默认获取经纬度" + r.point.lng + "," + r.point.lat);
            map.centerAndZoom(r.point, 18);
          } else {
            console.log("failed" + this.getStatus());
          }
        },
        { enableHighAccuracy: true }
      );

      map.addControl(new BMap.NavigationControl());
      map.addControl(new BMap.ScaleControl());
      map.addControl(new BMap.OverviewMapControl());
      map.addControl(new BMap.GeolocationControl());
      //添加地图类型控件
      map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP]
        })
      );
      // 设置地图显示的城市 此项是必须设置的
      //   map.setCurrentCity("北京");
      //开启鼠标滚轮缩放
    },
    info() {
      console.log();
    }
  },
  mounted() {
    this.createMap();
  }
};
</script>
<style scope  lang="less">
#map {
  min-height: 100vh;
  .BMapLabel {
    background-color: rgb(25, 137, 250) !important;
    color: #fff !important;
    border: none !important;
  }
}
.yymarker {
  width: 20vw;
  height: 20vh;
  background: pink !important;
}
.van-search {
  position: fixed;
  padding: 0.1rem 0.32rem;
  z-index: 99999;
  width: 100%;
  background: #df0a20 !important;
}
.van-search__action {
  color: #fff;
}
.van-search__action:active {
  background: #df0a20 !important;
}
body {
  margin: 0 !important;
}
</style>

